// axios
import axios from "axios";
// axios.defaults.baseURL = "http://47.100.222.240:3000/";
axios.defaults.timeout = 10000;
axios.defaults.headers.post["Content-Type"] =
  "application/x-www-form-urlencoded;charset=UTF-8";
import store from "@/store/index";
import Vue from "vue";
import { Message } from "element-ui";
import QS from 'qs'; 
import Z from '@/util/localStorage.js'
// console.log(Z)
Vue.use(Message);
axios.interceptors.request.use(
  config => {
    // vuextoken
    // httpheadertokentoken
    // tokentoken
    // const token = store.state.token;
    let token = Z.getStorage('token');//token
    token && (config.headers['X_token'] = token) 
    // token && (config.headers.Authorization = token);
    return config;
  },
  error => {
    return Promise.error(error);
  }
);
// tokentokenlocalStoragecookiemain.jstokentokenvuextokenheadertokentoken
// 
axios.interceptors.response.use(
  response => {
    // 200
    // 
    if (response.status === 200) {
      return Promise.resolve(response);
    } else {
      return Promise.reject(response);
    }
  },
  // 2
  // 
  // 
  // 
  error => {
    if (error.response.status) {
      switch (error.response.status) {
        // 401: 
        // 
        // 
        case 401:
          router.replace({
            path: "/login",
            query: {
              redirect: router.currentRoute.fullPath
            }
          });
          break;

        // 403 token
        // 
        // tokenvuextoken
        // 
        case 403:
          // Message({
          //   message: "",
          //   duration: 1000,
          //   showClose: true
          // });
          // token
          localStorage.removeItem("token");
          store.commit("loginSuccess", null);
          // fullPath
          setTimeout(() => {
            router.replace({
              path: "/login",
              query: {
                redirect: router.currentRoute.fullPath
              }
            });
          }, 1000);
          break;

        // 404
        case 404:
          // Message({
          //   message: "",
          //   duration: 1500,
          //   showClose: true
          // });
          break;
        // 
        default:
          // Message({
          //   message: error.response.data.message,
          //   duration: 1500,
          //   showClose: true
          // });
      }
      return Promise.reject(error.response);
    }
  }
);
/**
 * getget
 * @param {String} url [url]
 * @param {Object} params []
 */
let root = ''
if(process.env.NODE_ENV==='production'){
    root = 'http://47.100.222.240:3000'
}
console.log(root,process.env.NODE_ENV)
export function get(url, params){    
    return new Promise((resolve, reject) =>{        
        axios.get(root+url, {            
            params: params        
        }).then(res => {
            resolve(res.data);
        }).catch(err =>{
            reject(err.data)        
    })    
});}
/** 
 * postpost 
 * @param {String} url [url] 
 * @param {Object} params [] 
 */
export function post(url, params) {
    return new Promise((resolve, reject) => {
      axios.post(root+url, params)
        .then(res => {
            resolve(res.data);
        })
        .catch(err =>{
            reject(err.data)
        })
    });
}